import axios from 'axios'
import qs from 'qs'

import Vue from 'vue'
const v = new Vue() //jselementuivue

export default async function ajax (url,data={},type='get') {
  try {
    let res
    if(type === 'get') {
      res = await axios.get(url,{
        params : data
      })
    } else{
      res = await axios.post(url,qs.stringify(data))
    }
    res = res.data //axiospromiseawaitpromiseaxiosres
    return new Promise((resolve)=>{
      resolve(res) //promiseresolveawaitres
    })
  } catch (error) {
    //axios
    v.$message({ 
      message: 'Network Error',
      type: 'error'
      })
  }
}